@charset "UTF-8";
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #202020;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
}

::selection {
  background-color: orange;
  color: #fff;
}

/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "OpenSansRegular";
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  min-height: 700px;
  display: grid;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-bottom: 200px;
}

.header_fon:before {
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: -webkit-linear-gradient(25deg, orange, #996300);
  background: -o-linear-gradient(25deg, orange, #996300);
  background: linear-gradient(65deg, orange, #996300);
  border-radius: 0 0 50% 50% / 0 0 50% 50%;
  opacity: .8;
  z-index: -1;
  transform: scaleX(1.4);
  z-index: 1;
  content: '';
}

.header_fon_content {
  color: #000;
  z-index: 1;
  text-align: center;
}

.header_fon_content h1 {
  color: #000;
}

.block {
  padding: 50px 0;
}

.block_title {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.875rem;
  margin-bottom: 30px;
}

.portfolio_block1 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(150px, auto);
}

.portfolio_items1 {
  margin: 5px;
  border: 2px solid red;
  min-height: 2.5vh;
  background-position: center;
  background-size: cover;
  text-align: center;
  display: grid;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  color: red;
  font-weight: bold;
  position: relative;
  transition: color 0.2s ease;
  cursor: pointer;
}

.portfolio_items1:nth-child(1) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 4;
}

.portfolio_items1:nth-child(2) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
}

.portfolio_items1:nth-child(3) {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 4;
}

.portfolio_items1:nth-child(4) {
  grid-column-start: 4;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 4;
}

.portfolio_items1:nth-child(5) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 6;
}

.portfolio_items1:nth-child(6) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 5;
}

.portfolio_items1:nth-child(7) {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 4;
  grid-row-end: 6;
}

.portfolio_items1:nth-child(11) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 6;
  grid-row-end: 7;
}

.portfolio_items1:nth-child(9) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 5;
  grid-row-end: 7;
}

.portfolio_items1:nth-child(10) {
  grid-column-start: 4;
  grid-column-end: 4;
  grid-row-start: 5;
  grid-row-end: 7;
}

.portfolio_block2 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(170px, auto);
}

.portfolio_items2 {
  margin: 5px;
  border: 2px solid red;
  min-height: 2.5vh;
  background-position: center;
  background-size: cover;
  text-align: center;
  align-content: center;
  font-size: 22px;
  color: #fff;
  font-weight: bold;
  position: relative;
  transition: color 0.2s ease;
  cursor: pointer;
}

.portfolio_items2:nth-child(1) {
  grid-area: 1/1/4/1;
}

.footer_wrap {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
}

.footer_items {
  width: 30%;
  background-color: yellow;
}

.heagerclass {
  background-color: red;
  padding: 50px 0;
}

.heagerclass_title {
  color: #fff;
}

