  text-align: left;
  background-color: #fff;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 480px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (min-width: 568px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 600px) {
  .container {
    max-width: 580px;
  }
}

@media (min-width: 640px) {
  .container {
    max-width: 620px;
  }
}

@media (min-width: 667px) {
  .container {
    max-width: 640px;
  }
}

@media (min-width: 684px) {
  .container {
    max-width: 664px;
  }
}

@media (min-width: 734px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 736px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 800px) {
  .container {
    max-width: 760px;
  }
}

@media (min-width: 980px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.row {
  width: 100%;
}

.flexrow {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.clearfix:before,
.clearfix:after,
.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after {
  content: " ";
  display: table;
}

.clearfix:after,
.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}
